import React, { Component } from 'react';
import {NavLink} from 'react-router-dom';
import Media from "react-media";

export default class Header extends Component {

    title = (header) => (
            <div style={{
                position: "absolute",
                top: "15%",
                fontFamily: "Monospace",
                left: "4%"
            }}>
                <div className="container-fluid">
                    <div className="row text-center">
                        <div className="col-12">
                            <h1 style={{
                                color: "#ededed",
                                textShadow: "black 0 1px 1px"
                            }}>
                                {header.title}
                            </h1>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-12 text-center">
                            <h4 style={{
                                color: "#aeaeae",
                                textShadow: "black 0 1px 1px",
                                fontWeight: 100
                            }}>
                                {header.subtitle}
                            </h4>
                        </div>
                    </div>
                    <Media query="(max-width: 768px)">
                            {
                                (m) => 
                                    m?null: (
                                        <div className="container p-5 mt-4" style={{
                                            background: "rgba(0, 0, 0, 0.2)"
                                        }}>
                                            <div className="row">
                                                <div className="col-12">
                                                    {
                                                        (header.description || []).map((el, i) => (
                                                            <div style={{
                                                                display: "block",
                                                                color: "wheat",
                                                                fontFamily: "Courier New, sans-serif"
                                                            }} key={`${i + 1}`}>{el}</div>
                                                            ))
                                                        }
                                                </div>
                                            </div>
                                        </div>
                                    )
                            }
                    </Media>
                </div>
            </div>
        )

    social = (header) => (
            <div style={{
                position: "absolute",
                bottom: "10%",
                left: "50%",
                transform: "translateX(-50%)",
                textAlign: "center"
            }}>
                
                {
                    (header.social || []).map((el, i) => (
                            <a key={`${i+1}`} href={el.url} style={{fontSize: "xx-large", marginRight:"10px"}}>
                                <i style={{color: "#BB5251"}} className={`${el.icon}`}></i>
                            </a>
                        ))
                }
                
            </div>
        );

    render() {
        const { header } = this.props.data;
        return (
            <div id="header">
                <div className="backer"><div><NavLink to="/">Go Back</NavLink></div></div>

                <img style={{
                    width: "100%",
                    height: "100vh",
                    boxSizing: "border-box"
                }} src="./assets/img/header.jpg" alt="background" />
                <div style={{
                    position: "absolute",
                    top: 0,
                    width: "100%",
                    height: "100%",
                    background: "rgba(200, 200, 200, 0.3"
                }} />

                {this.title(header)}

                {this.social(header)}
            </div>
        );
    }
}
