import React, { Component } from 'react';
import { Line } from 'rc-progress';


export default class Resume extends Component {
        title = (title) => (
            <div>
                <h1>
                    {title} 
                </h1>
                <div style={{
                    width: "20%",
                    borderBottom: "2px solid #2b2b2b"
                }}/>
                <br />
                <br />
            </div>
        )

        education = (education) => (
            <div className="container-fluid education">
            
                {this.title("Education")}
                {
                    (education || []).map((el, i) => (
                        <div className="row" key={i}>
                            <div className="col-md-10 mx-auto">
                                <h4>
                                <i className="fa fa-graduation-cap"/>&nbsp;&nbsp;{el.name}
                                </h4>
                                <div className="row">
                                    <div className="col-11 mx-auto" style={{color: "#555"}}>
                                        <h6 style={{color: "#777"}}><i className="fa fa-circle" style={{fontSize: "7px"}} />&nbsp;&nbsp;{el.date}</h6>
                                        <h5>{el.degree}</h5>
                                        <h6>GPA: {el.gpa}</h6>
                                    </div>
                                </div>
                            </div>
                        </div>      
                    ))
                }
            </div>
        )

        experience = (experience) => (
            
            <div className="container-fluid experience">
                {this.title("Experience")}
                {
                    (experience || []).map((el, i) => (
                        <div className="row" key={i}>
                            <div className="col-md-10 mx-auto">
                                <h3>
                                    <i className="fa fa-briefcase"/>&nbsp;&nbsp;{el.role}
                                </h3>
                                <div className="row">
                                    <div className="col-11 mx-auto">
                                        <div style={{display: "inline-block"}}>
                                            <img style={{
                                                width: 25,
                                                borderRadius: "100%",
                                                filter: "opacity(75%)"
                                            }} src={el.photo} alt={el.name}/>
                                        </div>
                                        &nbsp;&nbsp;
                                        <h4 style={{display: "inline-block"}}>
                                            <a href={el.url} rel="noopener noreferrer" target="_blank">{el.name}</a>
                                        </h4>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-10 mx-auto" style={{color: "#555"}}>
                                        <h6 style={{color: "#777"}}><i className="fa fa-circle" style={{fontSize: "7px"}} />&nbsp;&nbsp;{el.date}</h6>
                                        <h6>{el.location}</h6>
                                        {
                                            (el.description || []).map((ell ,ii) => (
                                                    <div key={ii}>
                                                        <div>{ell}</div>    
                                                    </div>
                                                ))
                                        }
                                    </div>
                                </div>
                                <br />    
                            </div>
                        </div>  
                    ))
                }
            </div>
        )   

        skills = (skills) => {
            const rows = (name, icon, type) => (
                <div className="row">
                    <div className="col-md-8 col-11 mx-auto">
                        <div style={{color: "#7a7a7a"}}><i className={`fa ${icon}`}/>&nbsp;&nbsp;<h5 style={{display: "inline-block"}}>{name}</h5><br /></div>
                        {
                            (type || []).map((el, i) => (
                                <div key={i}>
                                        <br /><br />
                                        <div className="text-center mb-2" style={{fontFamily: "'Comic Sans MS', cursive, sans-serif"}}>{el.name}{(name==="Language")?<span style={{color: "#bbb"}}>{` (${el.level})`}</span>:null}</div>
                                        <Line percent={el.amount} strokeWidth="2" strokeColor={(el.amount<50)?"#FFCC80":"#A5D6A7"}  />
                                </div>
                            ))
                        }
                    </div>
                </div>
            );
            return (
                <div className="container-fluid experience">
                    {this.title("Skills")}
                    {rows("Industry Knowledge", "fa-toolbox", skills.industry_knowledge)}
                    <br /><hr /><br />
                    {rows("Tools And Technologies", "fa-code", skills.tools_and_technologies)}
                    <br /><hr /><br />
                    {rows("Others", "fa-circle-notch", skills.others)}
                    <br /><hr /><br />
                    {rows("Language", "fa-globe-asia", skills.language)}
                    <br /><br />
                </div>
            );
        }
        
        projects = (projects) => (
            <div className="container-fluid experience">
                {this.title("Projects")}
                {
                    (Object.keys(projects).reverse()).map((el, i) => (
                        <div key={i} className="row mb-5">
                            <div className="col-12">
                                <h4>
                                    {`${el.split(/[0-9]/)[0][0].toUpperCase()+el.split(/[0-9]/)[0].substr(1)} ${el.replace(el.split(/[0-9]/)[0], "")}`}
                                </h4>
                                <h6 style={{color: "grey"}}>{projects[el].date}</h6>
                                <br />
                            </div>
                            {
                                ((projects[el]).work || []).map((ell, ii) => (
                                    <div key={ii} className="col-12 mb-3">
                                        <div className="row">
                                            <div className="col-md-11 mx-auto">
                                                {ell.icon==="fas fa-user"?<span>&nbsp;</span>:""}<i className={ell.icon}></i>&nbsp;&nbsp;<span>{ell.for}</span>
                                                <div className="row">
                                                    <div className="col-md-11 mx-auto mt-2" style={{color: "#6a6a6a"}}>
                                                        {ell.name}&nbsp;&nbsp;<a href={ell.url} target="_blank" rel="noopener noreferrer"><i className="fa fa-external-link-alt" /></a>
                                                    </div>
                                                </div>
                                                <div className="pt-2 mb-2" style={{width: "70%", textAlign: "left", borderBottom: "1px solid #d9d9d9"}} />
                                            </div>
                                        </div>
                                    </div>
                                ))
                            }
                            <br />
                        </div>
                    ))
                }
            </div>
        )

        render() {
            const {resume} = this.props.data; 
            return (
            <div id="resume">
                {this.education(resume.education)}
                <br /><br /><br />
                {this.experience(resume.experience)}
                <br /><br /><br />
                {this.skills(resume.skills)}
                <br /><br /><br />
                {this.projects(resume.projects)}
            </div>
        );
    }
}
