import React, { Component } from 'react';

export default class About extends Component {
    render() {
        const {about} = this.props.data;
        return (
            <div id="about">
                <div className="container">
                    <div className="row">
                        <div className="col-md-2 mb-3">
                            <a href="https://alirezakay.github.io/Docs/Alireza-Kavian-Resume.pdf" role="button" className="btn btn-outline-danger" style={{color: "#eee"}}>Download Resume</a>
                        </div>
                        <div className="col-md-8 mx-auto">
                            <h4 style={{color: "#eee"}}>
                                About
                            </h4>
                            <div>
                                {
                                    (about.description || []).map((el, i) => (
                                        <div style={{
                                            display: "block",
                                            color: "#999",
                                            lineHeight: "1.8em",
                                            fontFamily: "cursive, sans-serif"
                                        }} key={`${i + 1}`}>{el}</div>
                                        ))
                                }
                                <h6 style={{color: "wheat", marginTop: "5px"}}>
                                    Age: {new Date().getFullYear() - 1997}
                                </h6>
                                <br />
                            </div>
                            <h4 style={{color: "#eee"}}>
                                Address
                            </h4>
                            <div>
                                {
                                    (about.contact || []).map((el, i) => (
                                        <div style={{
                                            display: "block",
                                            color: "#999",
                                            lineHeight: "1.8em",
                                            fontFamily: "cursive, sans-serif"
                                        }} key={`${i + 1}`}>{el}</div>
                                        ))
                                }
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
